import React, { Component } from 'react';
import { connect } from 'react-redux';

import { deleteInfluencersGroup } from "../../actions";

class ConfirmDeletionListModal extends Component {

    deleteInfluencersGroup(list_selected, deleteModalId) {
        window.$("#" + deleteModalId).modal("toggle");
        const groupUserInfo = {
            listSelected: this.props.listName,
            userCredentials: this.props.userCredentials
        }
        console.log(groupUserInfo);
        this.props.deleteInfluencersGroup(groupUserInfo);
    }
    render() {
        let deleteModalId = "confirmDeletionList-" + this.props.listName;
        return (
            <div className="modal fade" id={deleteModalId} tabIndex="-1" role="dialog" data-backdrop="false">
                <div className="modal-dialog modal-dialog-centered" role="document">
                    <div className="modal-content">
                        <div className="modal-header">
                            <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                                <span aria-hidden="true">&times;</span>
                            </button>
                        </div>
                        <div className="modal-body">
                            <p>Are you sure you want to delete this lists</p>
                        </div>
                        <div className="modal-footer">
                            <button type="button" className="btn btn-primary" onClick={() => { this.deleteInfluencersGroup(this.props.listName, deleteModalId) }}>Yes</button>
                            <button type="button" className="btn btn-secondary" data-dismiss="modal">Close</button>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

const mapStateToProps = state => ({
    userCredentials: state.userCredentials
});

const mapDispatchToProps = dispatch => {
    return {
        deleteInfluencersGroup: groupUserInfo => {
            dispatch(deleteInfluencersGroup(groupUserInfo));
        }
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(ConfirmDeletionListModal);